@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro&display=swap);
html{font-family:Source Sans Pro, sans-serif;box-sizing:border-box}.SplashScreen-main-div{display:flex;flex-direction:column;justify-content:center;align-items:center;padding:3rem;max-width:50rem;min-width:20rem;margin:0 auto;border-radius:20px;margin-top:5%;font-size:25px;font-weight:700}.SplashScreen-main-div button,.SplashScreen-main-div input{font-size:25px;font-weight:700;background-color:#e80d1e;border:none;color:white;margin:1em;border-radius:10px;text-align:center;padding:5px;width:100%}.SplashScreen-main-div .SplashScreen-join-room{display:flex;flex-direction:column;align-items:center;justify-content:center}.ChatBoard-main-div{display:flex;flex-direction:column}.ChatBoard-main-div button{background-color:#e80d1e;border:none;color:white;margin:1em;border-radius:10px;text-align:center;padding:5px;font-weight:700}.ChatBoard-main-div .ChatBoard-download{background-color:#e80d1e;width:50%;align-self:center}.ChatBoard-main-div .ChatBoard-info{display:flex;justify-content:center;align-items:center}.ChatBoard-main-div .ChatBoard-temp{display:flex;align-self:center}.ChatBoard-main-div .ChatBoard-write-message{display:flex;flex-direction:column;justify-content:center;width:50%;align-self:center;margin:2rem}.ChatBoard-main-div .ChatBoard-write-message textarea{font-family:"Source Sans Pro";border:1px solid black;border-radius:5px;background-color:#ffe5db;resize:none;padding:10px}.ChatBoard-main-div .ChatBoard-feedback-list{align-self:center;background-color:#57babb;padding:1rem;border-radius:10px;width:90%;text-align:center;max-height:50vh;overflow-y:scroll;overflow-x:hidden;word-wrap:break-word;color:black}.FeedbackMessage-main-div{background-color:#ffe5db;color:black;font-weight:500;border-radius:10px;font-size:20px;display:flex;flex-direction:column;margin-bottom:10px}.FeedbackMessage-main-div p{margin:5px}.FeedbackMessage-main-div button{margin:5px;width:20%}.FeedbackMessage-main-div .FeedbackMessage-votes{display:flex;align-self:center}

